<script setup>
import { computed, watch } from 'vue';
import ErrorsPlaceholder from "./errors_placeholder.vue";
import ComponentWrapper from "./component_wrapper.vue";

const props = defineProps({
  append: {
    type: String,
  },
  name: {
    type: String,
    require: true
  },
  id: {
    type: String,
    require: true
  },
  prepend: {
    type: String,
  },
  suggest_value: { // do not use directly (instead use the computed property suggestValue)
    type: [String, Boolean],
    require: false,
    default: false
  },
  display_error: { // do not use directly (instead use the computed property displayError)
    type: [String, Boolean],
    require: false,
    default: true
  }
})


const suggestValue = computed(() => {
  return (props.suggest_value === 'true' || props.suggest_value === true || props.suggest_value === "force")
})

const displayError = computed(() => {
  return (props.display_error === 'true' || props.display_error === true)
})

const useSuggestedValue = computed(() => {
  return (suggestValue && !inputTouched) || props.suggest_value === 'force'
})

const suggestedValue = computed(() => {
  return this.$store.getters.getSuggestedValues(props.name)
})

const inputClass = computed(() => {
  return {
    "input-block__field": true,
    "input-block__field--invalid": (inputTouched && inputError),
    "input-block__field--warning": (inputTouched && !inputError && inputWarning)
  }
})

const inputError = computed(() => {
  if (inputTouched) {
    return this.$store.getters.getError(props.name)
  } else {
    return null
  }
})

const inputWarning = computed(() => {
  if (inputTouched) {
    return this.$store.getters.getWarning(props.name)
  } else {
    return null
  }
})

const inputGroupClass = computed(() => {
  return {
    "input-block": true
  }
})

const inputTouched = computed({
  get () {
    return this.$store.getters.getTouched(props.name)
  },
  set (value) {
    this.$store.commit('setTouched', {
          value: value,
          name: props.name
        }
    )
  }
})

const inputValue = computed({
  get () {
    return this.$store.getters.getValue(props.name)
  },
  set (value) {
    this.$store.dispatch('update', {
          value: value,
          name: props.name
        }
    )
  }
})

watch(suggestedValue, (value, _oldValue) => {
  if (useSuggestedValue) {
    this.inputValue = value
  }
})

</script>


<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">

    <div v-if="prepend" class="input-block__prepend" :id="id + '__prepend'">
      <span v-html="prepend"></span>
    </div>

    <input v-bind="$attrs"
           :class="[inputClass, prepend ? 'input--has-prepend' : '', append ? 'input--has-append' : '']"
           :id="id"
           v-model="inputValue"
           @focus="inputTouched=true"
           :name="name"/>

    <div v-if="append" class="input-block__append" :id="id + '__append'">
      <span v-html="append"></span>
    </div>

    <ErrorsPlaceholder v-if='displayError' :names=[name]></ErrorsPlaceholder>
  </ComponentWrapper>
</template>
